    //     // console.log("dummy", dummy);
    //     const x = dummy[0] / kx;
    //     const y = dummy[1] / ky
    //     const dummyPoint = new OpenSeadragon.Point(x, y)
    //     var viewportPoint = this.viewer.viewport.pointFromPixel(dummyPoint);
    //     var point = this.viewer.viewport.viewportToImageCoordinates(viewportPoint);
    //     dummy[0] = point.x 
    //     dummy[1] = point.y 
    //     chunked_arr.push(dummy);	
    //     index += size;	
    //   }
    // return chunked_arr  
