    // const { canvas } = viewer.drawer
    // const canvasBounds = canvas.getBoundingClientRect();
    // const kx = canvas.width / canvasBounds.width;
    // const ky = canvas.height / canvasBounds.height;
    // const src = new cv.imread(canvas);
    // console.log("allContourPoints", src.size());
    // // Convert to grayscale & threshold
    // cv.cvtColor(src, src, cv.COLOR_RGB2GRAY, 0);
    // // cv.adaptiveThreshold(src, src, 200, cv.ADAPTIVE_THRESH_GAUSSIAN_C, cv.THRESH_BINARY, 5, 15);// Find contours
    // cv.threshold(src, src, 177, 200, cv.THRESH_BINARY);
    // const contours = new cv.MatVector();
    // const hierarchy = new cv.Mat();
    // cv.findContours(src, contours, hierarchy, cv.RETR_CCOMP, cv.CHAIN_APPROX_SIMPLE);
    // let allContourPoints = [];
    // for (let i = 0; i < contours.size(); ++i) {
    //   const polygon = new cv.Mat();
    //   const contour = contours.get(i);
    //   cv.approxPolyDP(contour, polygon, 0.01, true);
    //   allContourPoints = allContourPoints.concat(chunk(polygon.data32S, 2, kx, ky))
    // }
    // // for (let polyline of res.polylines) {
    // //   for (let point of polyline){
    // //     const x = point[0] / kx;
    // //     const y = point[1] / ky
    // //     const dummyPoint = new OpenSeadragon.Point(x, y)
    // //     var viewportPoint = this.viewer.viewport.pointFromPixel(dummyPoint);
    // //     var imgPoint = this.viewer.viewport.viewportToImageCoordinates(viewportPoint);
    // //     allContourPoints.push([imgPoint.x,imgPoint.y]);	
    // //   }
